@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Noto+Serif+JP:wght@200;300;400;500;600;700;900&display=swap);
:root{--text-color-100: #202124;--text-color-86: #3c4043;--text-color-71: #5f6368;--text-color-55: #80868b;--text-color-43: #9aa0a6;--text-color-16: #dadce0;--primary-color-100: #002c3d;--primary-color-86: #244a58;--primary-color-71: #4a6975;--primary-color-55: #738b94;--primary-color-43: #91a4ac;--primary-color-16: #d6dde0;--secondary-color: #009fb9;--highlight-color: #fa7f70;--background-color: #ffffff;--surface-color: #f0f4f5}.text-caption{font-family:"Lato";font-style:normal;font-weight:normal;font-size:14px;line-height:22px;color:#5f6368;color:var(--text-color-71);margin:0}html,body{margin:0;width:100%;height:100%}#root{height:100%;display:grid;grid-template-rows:96px auto}h1{font-family:Lato;font-style:normal;font-weight:bold;font-size:64px;line-height:76px;color:#002c3d;color:var(--primary-color-100);margin:0;margin-bottom:16px}h2{font-family:Lato;font-style:normal;font-weight:normal;font-size:56px;line-height:64px;color:#002c3d;color:var(--primary-color-100);margin:0;margin-bottom:16px}h3{font-family:Lato;font-style:normal;font-weight:normal;font-size:44px;line-height:52px;color:#002c3d;color:var(--primary-color-100);margin:0;margin-bottom:16px}h4{font-family:Lato;font-style:normal;font-weight:normal;font-size:36px;line-height:44px;color:#002c3d;color:var(--primary-color-100);margin:0;margin-bottom:16px}h5{font-family:Lato;font-style:normal;font-weight:normal;font-size:32px;line-height:40px;color:#002c3d;color:var(--primary-color-100);margin:0;margin-bottom:16px}h6{font-family:Lato;font-style:normal;font-weight:normal;font-size:28px;line-height:36px;color:#002c3d;color:var(--primary-color-100);margin:0;margin-bottom:16px}figcaption{font-family:"Lato";font-style:normal;font-weight:normal;font-size:14px;line-height:22px;color:#5f6368;color:var(--text-color-71);margin:0}p{font-family:"Noto Serif JP";font-style:normal;font-weight:normal;font-size:16px;line-height:24px;color:#3c4043;color:var(--text-color-86);margin:0;margin-bottom:16px}.link{text-decoration:none}.link:hover,.link:active,.link:visited{color:inherit}

.app-content{display:flex;flex-direction:vertical;flex-grow:1}

.action-bar{display:flex;flex-direction:row;flex-grow:1;justify-content:space-between;align-items:center;padding:0 16px}.action-bar-logo{height:40px}


:root{--text-color-100: #202124;--text-color-86: #3c4043;--text-color-71: #5f6368;--text-color-55: #80868b;--text-color-43: #9aa0a6;--text-color-16: #dadce0;--primary-color-100: #002c3d;--primary-color-86: #244a58;--primary-color-71: #4a6975;--primary-color-55: #738b94;--primary-color-43: #91a4ac;--primary-color-16: #d6dde0;--secondary-color: #009fb9;--highlight-color: #fa7f70;--background-color: #ffffff;--surface-color: #f0f4f5}.tabs-tab{text-transform:uppercase;display:inline-block;margin:8px 4px;padding:4px;border-radius:4px;cursor:pointer}.tabs-tab.active{border-bottom:solid 4px #009fb9;border-bottom:solid 4px var(--secondary-color)}.tabs-tab:hover{background-color:#f0f4f5;background-color:var(--surface-color)}

